import nodeMailer from 'nodemailer';

export default async function getTransporter() {
  try {
    const transporter = await nodeMailer.createTransport({
      host: 'smtp.gmail.com',
      port: 587,
      secure: false,
      auth: {
        type: 'login',
        user: 'realgov.test@gmail.com',
        pass: 'Resourceone@1',
      },
    });
    return transporter;
  } catch (err) {
    console.log(err);
    return err;
  }
}
